

.analitycs{
    width: 33%;
    height: 314px;
    border: 1px solid #212734 ;
    background-color:rgba(33, 39, 52, 1);
    border-radius: 7px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    margin-left: 17px;
    padding: 10px 37px 20px 37px;
    &__name{ 
        display: flex;
        width: 100%;
        justify-content: flex-start;
        align-items: center;
        padding-top: 18px;
        position: relative;
        
        span{
            font-weight: 400;
            font-size: 20px;
            line-height: 30px;
            color: rgba(255, 255, 255, 1);
        }
        a{
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            text-decoration: none;
            color: #805AD5;
            transition:  all .3s;
            position: absolute;
            right: 20px;
            top: 10px;
            z-index: 10px;
            &:hover{
                color: #5932ad;

            }
        }
    }

    &__buttons{

        div{
            width: 100%;
            height: 47px;
            border-radius: 10px;
            border: 1px solid rgba(128, 90, 213, 1);
            box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.25);
            margin-top: 10px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            &:hover{
                background-color: #5932ad;
            }
            p{
                color: 
                rgba(153, 111, 248, 1);
                font-weight: 400;
                font-size: 10px;
                line-height: 22px;
            }

            a{
                font-weight: 400;
                font-size: 10px;
                line-height: 22px;
                color: #fff;
                text-decoration: none;
            }
        }

      
        .count{
            img{
            }

            a{
                text-decoration: none;

            }
        }

        .exchang{
            img{
                margin-left: 20px;

            }
            p{
                line-height: 10px !important;
                margin-left: 20px;
            }
            a{
                display: flex;
                align-items: center;
                svg{
                    margin-right: 3px;
                }
            }
        }

        .withdrow{

        }

        .paid{

        }
    }
}