



.header{
    max-width:100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 54px;
    background-color: rgba(33, 40, 52, 1);
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    &__logo{
        position: relative;
       
        img{
           
            cursor: pointer;
            &:nth-child(2){
                position: absolute;
                top: 9px;
                left: 15px;
            }

            
        }
        
    }

    &__menu{
        nav{
            ul{
                display: flex;
                
                li{
                    width: 125px;
                    height: 47px;
                    cursor: pointer;
                    border-radius: 10px;
                    display: flex;
                    align-items: center;
                    padding: 12px 0 8px 25px;
                    margin-left: 37px;
                    svg{
                        width: 15px;
                        height: 15px;
                    }
                    @media screen and (max-width:1600px) {
                        margin-left: 5px;
                    }
                    &:hover{
                        background-image: linear-gradient( 0.25turn,rgba(163, 125, 249, 1),
                        rgba(123, 90, 197, 1));

                    }
                    a{
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 20px;
                        text-decoration: none;
                        text-transform: capitalize;
                        color: rgba(255, 255, 255, 1);
                        svg:nth-child(1){
                            margin-right: 8px;
                        }
                        svg:nth-child(2){
                            margin-left: 10px;
                            cursor: pointer;
                        }
                    }
                }
            }
        }
    }

    &__user{
        display: flex;
        align-items: center;
        a{
            text-decoration: none;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: rgba(165, 136, 231, 1);
            color: rgba(255, 255, 255, 1);
            font-size: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 22px;
        }

        svg{
            cursor: pointer;
        }
    }
}



